    return {
      videoList: [
        { name: "Êñ∞‰∏≠ÂõΩ.mp4", url: "https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E4%BA%8C%E6%9C%9F/%E7%AC%AC%E4%BA%8C%E6%9C%9F/%E9%A1%B9%E7%9B%AE%E6%88%90%E6%9E%9C%20%E5%AE%A3%E4%BC%A0%E7%BB%84/%E6%96%B0%E4%B8%AD%E5%9B%BD.mp4" },
        { name: "Ê∞ëÂõΩ.mp4", url: "https://llzm-lixiaobao.oss-cn-beijing.aliyuncs.com/%E4%BA%8C%E6%9C%9F/%E7%AC%AC%E4%BA%8C%E6%9C%9F/%E9%A1%B9%E7%9B%AE%E6%88%90%E6%9E%9C%20%E5%AE%A3%E4%BC%A0%E7%BB%84/%E6%B0%91%E5%9B%BD.mp4" },
	  ],
      currentPlaying: -1, // ÂΩìÂâçÊí≠ÊîæÁöÑËßÜÈ¢ëÁ¥¢Âºï
      videoCtxCache: {} // ÁºìÂ≠òËßÜÈ¢ë‰∏ä‰∏ãÊñá
    };
  },
  methods: {
    // Â§ÑÁêÜËßÜÈ¢ëÊí≠Êîæ‰∫ã‰ª∂
    handlePlay(index) {
      // ÈÅøÂÖçÈáçÂ§çÂ§ÑÁêÜÂêå‰∏ÄËßÜÈ¢ë
      if (this.currentPlaying === index) return;

      // ÊöÇÂÅú‰πãÂâçÊí≠ÊîæÁöÑËßÜÈ¢ë
      if (this.currentPlaying !== -1) {
        // ÂÆâÂÖ®Ëé∑ÂèñËßÜÈ¢ë‰∏ä‰∏ãÊñáÔºàÂÖºÂÆπÂä®ÊÄÅÂàõÂª∫Âú∫ÊôØÔºâ
        const prevCtx = this.videoCtxCache[this.currentPlaying] || uni.createVideoContext(`video-${this.currentPlaying}`, this);
        prevCtx.pause();
      }

      // Êõ¥Êñ∞ÂΩìÂâçÊí≠ÊîæÁ¥¢Âºï
      this.currentPlaying = index;
      // ÁºìÂ≠òÂΩìÂâçËßÜÈ¢ë‰∏ä‰∏ãÊñá
      this.videoCtxCache[index] = uni.createVideoContext(`video-${index}`, this);
    }
  }
};
